import './App.css';
import React from 'react'

function Home () {
    // eslint-disable-next-line no-undef
    const Leaflet = L;
    const preFolderName = 'kartes';
    var map = Leaflet.map('map', {
        crs: Leaflet.CRS.Simple,
        minZoom: -5
    });

    var bounds = [[0,0], [1000,1000]];
    var stavs1 = Leaflet.imageOverlay(preFolderName + '/zm1.svg',bounds).addTo(map);
    var stavs2 = Leaflet.imageOverlay(preFolderName + '/zm2.svg',bounds).addTo(map);
    var stavs3 = Leaflet.imageOverlay(preFolderName + '/zm3.svg',bounds).addTo(map);
    var stavs4 = Leaflet.imageOverlay(preFolderName + '/zm4.svg',bounds).addTo(map);
    var stavs5 = Leaflet.imageOverlay(preFolderName + '/zm5.svg',bounds).addTo(map);

    var baseMaps = {
        "1": stavs1,
        "2": stavs2,
        "3": stavs3,
        "4": stavs4,
        "5": stavs5
    };

    Leaflet.control.layers(baseMaps).addTo(map);

    map.fitBounds(bounds);

    return ("")
}

export default Home;
